.project {
  display: flex;
  flex-direction: column;
  align-items: center;

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: center;

    li {
      border: solid 2px #00e9ff;
      border-radius: 10px;
    }
  }
  .Box1 {
    cursor: pointer;
    .image {
      z-index: 1;
    }
    h3,
    .title {
      opacity: 0;
    }
    &:hover {
      background-color: #030325;
      .image {
        opacity: 0.2;
        z-index: 0;
      }
      h3 {
        opacity: 1;
        z-index: 1;
        color: rgb(255, 255, 255);
      }
      .title {
        opacity: 1;
        z-index: 1;
      }
    }
  }

  .box {
    gap: 1.75rem;
  }
}
